div.create-post {
  padding-top: 20px;
}
div.create-post input,
div.create-post textarea {
  background: #f5f5f5;
  border: 0;
  width: 100%;
}
div.create-post input {
  margin: 10px 0;
  line-height: 1.6;
}
div.create-post input.title {
  font-size: 30px;
}
div.create-post input.summary {
  font-size: 20px;
}
div.create-post textarea {
  height: 400px;
}


